.formPart {
	margin-top: 100px;
	padding: 21px 425px 127px 408px;
}

.modalParent {
	position: relative;
}

.formH2 {
	color: #42454a;
	text-align: center;
	font-size: 50px;
	font-weight: 700;
	line-height: 120%;
	margin-bottom: 11px;
}
.formP {
	color: #42454a;
	text-align: center;
	font-size: 20px;
	font-weight: 400;
	line-height: 140%;
	margin-bottom: 60px;
}

.formInput {
	width: 400px;
	border-radius: 10px;
	border: 1.5px solid #f5f5f5;
	background: #fafafa;
	padding: 30px;
	margin-right: 20px;
	border: 1px solid #42454a;
	font-size: 20px;
	font-weight: 400;
	line-height: 140%;
}
.formInput::placeholder {
	color: #42454a;
	font-size: 20px;
	font-weight: 400;
	line-height: 140%;
}

.formBtn {
	border-radius: 10px;
	background: #42454a;
	padding: 27px 57px 26px 58px;
	color: #fbfbfb;
	font-size: 20px;
	font-weight: 400;
	line-height: 140%;
	border: none;
	cursor: pointer;
}

.modalPopup {
	height: 80px;
	border-radius: 15px;
	display: flex;
	flex-wrap: wrap;
}
