.cardsComp {
	margin-top: 250px;
}

.cardZone {
	display: flex;
	gap: 58px;
}

.InfoComp {
	min-width: 180px;
	padding: 30px;
	border-radius: 6.606px;
	border: 0.661px solid #f4f4f4;
	background: #fff;
	box-shadow: 0px 13.21117px 26.42234px 0px rgba(0, 0, 0, 0.06);
}
.InfoCompNum {
	color: #42454a;
	text-align: center;
	font-size: 40px;
	font-weight: 700;
	line-height: 100%;
}
.InfoCompSys {
	color: #42454a;
	font-size: 18px;
	font-weight: 700;
	line-height: 100%;
	margin-left: 10px;
}
.InfoCompText {
	margin-top: 15px;
	color: #42454a;
	text-align: center;
	font-size: 13px;
	font-weight: 400;
	line-height: 130%;
}
