.nav {
	display: flex;
}

.header {
	padding: 27px 0 26px;
	border-bottom: 1px solid #42454a;
}

.headerWrapper {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.headerNavUl {
	display: flex;
	gap: 75px;
}

.logo {
	width: 69px;
	height: 47px;
}

.headerNavItem {
	list-style-type: none;
	color: #42454a;
	text-transform: uppercase;
	font-size: 14px;
	font-weight: 400;
	cursor: pointer;
}

.headerNavBtns {
	display: flex;
	gap: 15px;
}

.btn {
	border-radius: 4px;
	background: #42454a;
	box-shadow: 0px 12px 25px -10px rgba(66, 69, 74, 0.3);
	padding: 14px 26px 18px;
	color: #fff;
	border: none;
	text-transform: uppercase;
	cursor: pointer;
}

.login {
	background: #fff;
	color: #42454a;
	box-shadow: 0px 12px 25px -10px rgba(0, 0, 0, 0.04);
}
