.testimItem {
	cursor: pointer;
	border: 1px solid #d8d8d8;
	width: 360px;
	height: 360px;
	background: #fafafa;
	display: flex;
	flex-direction: column;
	padding: 52px 22px 50px 34px;
	margin-bottom: 50px;
	transition: transform 0.7s ease-in-out;
}
.testimItem:hover {
	transform: scale(1.1);
	background: #fff;
	box-shadow: 0px 1.85185px 3.14815px 0px rgba(49, 49, 49, 0.02),
		0px 20px 13px 0px rgba(49, 49, 49, 0.05),
		0px 38.51852px 25.48148px 0px rgba(49, 49, 49, 0.06);
}

.testimItem:hover .testimItemFooter {
	transform: translateX(-65px);
}
.testimItem:hover .testimStars {
	transform: translateX(-105px);
}
/* .testimItem:hover .testimText {
	text-align: left;
} */

.testimStars {
	width: 96px;
	margin: 0 auto;
	margin-bottom: 21px;
	transition: transform 0.7s ease-in-out;
}

.testimText {
	text-align: center;
	width: 304px;
	min-height: 140px;
	color: #42454a;
	font-size: 20px;
	font-weight: 400;
	line-height: 140%;
	margin-bottom: 50px;
}

.testimItemFooter {
	margin: 0 auto;
	display: flex;
	gap: 20px;
	transition: transform 0.7s ease-in-out;
}

.testimItemFooter > img {
	width: 48px;
	height: 48px;
	border-radius: 100%;
}

.testimItemFooterText {
	text-align: center;
}

.testimItemFooterText > p {
	color: #42454a;
	font-size: 16px;
	font-weight: 500;
	margin-bottom: 4px;
}

.testimItemFooterText > span {
	color: #42454a;
	font-size: 14px;
	font-weight: 400;
}
