.footer {
	background: #42454a;
	margin-top: 80px;
	padding-bottom: 30px;
}

.footerCon {
	position: relative;
	display: flex;
	justify-content: flex-end;
}

.footerImg {
	position: absolute;
	top: -92px;
	left: -90px;
}

.footerInfo {
	padding-top: 100px;
	padding-right: 150px;
}
.columns {
	display: flex;
	gap: 100px;
	text-align: left;
}

.column {
	display: flex;
	flex-direction: column;
	gap: 35px;
}
.h3 {
	color: #fbfbfb;
	font-size: 22px;
	font-weight: 700;
	margin-bottom: 35px;
}
.footerTextLi {
	color: #fbfbfb;
	font-size: 16px;
	font-weight: 400;
	cursor: pointer;
}
.icons {
	margin-top: -20px;
	margin-left: 68%;
}
.copyRight {
	display: block;
	color: #fbfbfb;
	font-size: 16px;
	font-weight: 400;
	margin-top: 80px;
}
