.hero {
	margin-top: 34px;
	background: url(../img/scooter.png) no-repeat top right;
	background-size: 50%;
}

.heroTitle {
	color: #42454a;
	font-size: 90px;
	font-weight: 300;
	line-height: 95px;
	letter-spacing: 0.9px;
	text-transform: uppercase;
	text-align: left;
}

.heroSpan {
	color: #42454a;
	font-size: 90px;
	font-weight: 700;
	line-height: 95px;
	letter-spacing: 0.9px;
	text-transform: uppercase;
}

.empty {
	border: 1px solid #42454a;
	margin-top: 93px;
	width: 335px;
}

.heroP {
	color: #42454a;
	width: 353px;
	font-size: 22px;
	font-weight: 400;
	line-height: 34px;
	letter-spacing: 0.22px;
	text-align: left;
	margin-top: 62px;
}

.heroFooter {
	margin-top: 50px;
	padding-left: 80px;
	align-items: left;
	width: 600px;
	display: flex;
	gap: 80px;
	margin-bottom: 160px;
}

.heroFooterBtn {
	color: #17181a;
	font-size: 20px;
	font-weight: 700;
	line-height: 32px;
	letter-spacing: 0.4px;
	margin-left: 15px;
	border: none;
	background: none;
	cursor: pointer;
	position: relative;
}

.heroFooterBtn::before {
	content: url('../img/Vector.svg');
	background: #42454a;
	width: 80px;
	height: 80px;
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	top: 0;
	left: -95px;
	border-radius: 10px;
}

.heroFooterBtn:hover:before {
	content: url('../img/Vector.svg');
	transform: translateX(97px) /*scaleX(2)*/;
	transition-duration: 1s;
	color: #42454a;
	z-index: 1;
	border-radius: 100%;
}
.heroFooterBtn:hover {
	color: #fff;
	transition-duration: 0.8s;
}

.heroFooterP {
	color: #414449;
	width: 87px;
	font-size: 18px;
	font-weight: 500;
	line-height: 25px;
	letter-spacing: 0.18px;
	text-align: left;
}

.heroFooterP::after {
	content: url('../img/Vector 232.png');
}

